<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilterVue :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoryId" :color-id.sync="filterColor" />
      <section class="catalog">

        <div v-if="productsLoading">Загрузка товаров, подождите пожалуйста...
        <div class="spinner"></div>
      </div>
        <div v-if="productsLoadingFailed">Произшла ошибка при загрузке товаров <button @click="loadProducts">Попробуйте
            еще раз</button></div>

        <ProductListVue :products="products" />
        <BasePaginationVue v-model="page" :count="countProducts" :per-page="productsPerPage" />

      </section>
    </div>
  </main>
</template>

<script>
import ProductListVue from '@/components/ProductList.vue';
import BasePaginationVue from '@/components/BasePagination.vue';
import ProductFilterVue from '@/components/ProductFilter.vue';
import axios from 'axios'

export default {
  components: { ProductListVue, BasePaginationVue, ProductFilterVue },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColor: 0,

      page: 1,
      productsPerPage: 3,

      productsData: null,

      productsLoading: false,
      productsLoadingFailed: true,
    }
  },
  computed: {
    products() {
      return this.productsData
        ? this.productsData.items.map(product => {
          return {
            ...product,
            image: product.image.file.url
          }
        }) : [];

    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    }
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
       return axios.get(`https://vue-study.skillbox.cc/api/products`, {
          params: {
            page: this.page,
            limit: this.productsPerPage,
            categoryId: this.filterCategoryId,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
            colorId: this.filterColor
          }
        })
          .then(response => this.productsData = response.data)
          .catch(() => this.productsLoadingFailed = true)
          .then(() => this.productsLoading = false)
      }, 2000);

    }
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColor() {
      this.loadProducts();
    },
  },
  created() {
    this.loadProducts();
  }
}
</script>
<style>

.spinner {
  position: absolute;
  left: 500px;
  right: 500px;
  width: 100px;
  color: rgb(11, 228, 66);
  display: flex;
  min-height: 100vh;
}

.spinner:after {
  animation: changeContent .9s linear infinite;
  display: block;
  content: "⠋";
  font-size: 80px;
}

@keyframes changeContent {
  10% { content: "⠙"; }
  20% { content: "⠹"; }
  30% { content: "⠸"; }
  40% { content: "⠼"; }
  50% { content: "⠴"; }
  60% { content: "⠦"; }
  70% { content: "⠧"; }
  80% { content: "⠇"; }
  90% { content: "⠏"; }
}
</style>

