<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" @click.prevent="decrementItem()">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model.number="amount">

    <button type="button" aria-label="Добавить один товар" @click.prevent="incrementItem()">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  props: ['num'],
  data() {
    return { amount: 1 }
  },
  methods: {
    incrementItem() {
      this.$emit('change', this.amount += 1)
    },
    decrementItem() {
      if (this.amount > 1) {
        this.$emit('change', this.amount -= 1)
      }
    },
  },
  model: {
    prop: 'num',
    event: 'change',
  },
  mounted() {
    if (this.num) { this.amount = this.num }
  },

}
</script>

