<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ $store.state.cartProducts.length }} товара
      </span>
    </div>
    <div v-if="$store.state.cartLoading">Загрузка товаров, подождите пожалуйста...
      <div class="spinner"></div>
    </div>
    <div v-if="$store.state.cartLoadingFailed">Произшла ошибка при загрузке товаров <button
        @click="loadProducts">Попробуйте
        еще раз</button></div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">

            <CartItemVue v-for="item in products" :key="item.productId" :item="item" />

          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link tag="button" :to="{ name: 'order' }" class="cart__button button button--primery" type="submit"
            :disabled="emptyCart()">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
import CartItemVue from '@/components/CartItem.vue';

export default {

  filters: {
    numberFormat
  },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),
  },
  components: { CartItemVue },
  methods: {
    emptyCart() {
      if (this.$store.state.cartProducts.length === 0)
        return true
    }
  }
}
</script>
