import Vue from "vue";
import VueRouter from 'vue-router';
import MainPageVue from '@/pages/MainPage';
import ProductPageVue from '@/pages/ProductPage';
import NotFoundPage from '@/pages/NotFoundPage';
import CartPage from '@/pages/CartPage';
import OrderPage from '@/pages/OrderPage';
import OrderInfoPage from '@/pages/OrderInfoPage';

Vue.use(VueRouter);

const routes = [
  {name: 'main', component: MainPageVue, path: '/'},
  {name: 'product', component: ProductPageVue, path: '/products/:id'},
  {name: 'cart', component: CartPage, path: '/cart'},
  {name: 'order', component: OrderPage, path: '/order'},
  {name: 'orderInfo', component: OrderInfoPage, path: '/order/:id'},
  {name: 'notfound', component: NotFoundPage, path: '*'},
];
// создаем массив, где будут хранится наши маршруты,
// добавляются в виде объекта, path указываем '/' так как MainPage
// главная страница она будет являтся корнем нашего сайта.

const router = new VueRouter({
  routes
});
// подключаем к экземпляру роутера

export default router;
